* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Arial', 'Helvetica', sans-serif;
}

body {
    background-color: #6495ed;

    main {
        min-width: 100%;
        min-height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;

        .clock {
            min-width: 600px;
            min-height: 600px;
            background-color: #f0f8ff;
            border-radius: 100%;
            border: 10px solid #333;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;

            .number {
                font-size: 1.2rem;
                font-weight: 600;
                position: absolute;
                width: 100%;
                height: 100%;
                text-align: center;
                padding: 5px;
            }

            .number-1 {
                transform: rotate(30deg);
            }

            .number-2 {
                transform: rotate(60deg);
            }

            .number-3 {
                transform: rotate(90deg);
            }

            .number-4 {
                transform: rotate(120deg);
            }

            .number-5 {
                transform: rotate(150deg);
            }

            .number-6 {
                transform: rotate(180deg);
            }

            .number-7 {
                transform: rotate(210deg);
            }

            .number-8 {
                transform: rotate(240deg);
            }

            .number-9 {
                transform: rotate(270deg);
            }

            .number-10 {
                transform: rotate(300deg);
            }

            .number-11 {
                transform: rotate(330deg);
            }

            .number-12 {
                transform: rotate(360deg);
            }

            .pointer {
                position: absolute;
                transform-origin: bottom center;
            }

            .second-hand {
                width: 5px;
                height: 240px;
                background-color: brown;
                border-radius: 5px;
            }

            .minute-hand {
                width: 8px;
                height: 200px;
                background-color: #333;
                border-radius: 5px;
            }

            .hour-hand {
                width: 13px;
                height: 150px;
                background-color: #333;
                border-radius: 13px;
            }

            .center {
                width: 20px;
                height: 20px;
                border-radius: 20px;
                background-color: brown;
                position: absolute;
            }
        }
    }
}